import React from "react";
import styled from "styled-components";
import Typewriter from "typewriter-effect";
import Button from './Button';

const Title = styled.h2`
  font-size: ${(props) => props.theme.fontxxl};

  width: 70%;
  color: ${(props) => props.theme.text};
  align-self: flex-start;

  span {
    text-transform: uppercase;
    font-family: "Verdana", cursive;
    margin-top: 1000rem;
  }
  .text-1{
      color: #d14c3c;
      margin-top: 1000rem;
      margin-bottom: 1000rem;
  }
  .text-2{
      color: #d14c3c;
  }
  .text-3{
      color: #d14c3c;
  }

  @media (max-width: 70em; margin-top: 100px;) {
    font-size: ${(props) => props.theme.fontxl};
    margin-bottom: 1000rem;
  }
  @media (max-width: 48em) { 
    align-self: center;
    text-align:center;
    margin-bottom: 1000rem;
  }
  @media (max-width: 40em){
    width: 90%;
    margin-bottom: 1000rem;
  }

  
`;
const SubTitle = styled.h3`
  font-size: ${(props) => props.theme.fontlg};
  text-transform: capitalize;
  color: ${props => `rgba(${props.theme.textRgba}, 0.6)`};
  font-weight:600;
  margin-bottom: 1rem;
  width: 80%;
  align-self: flex-start;

  @media (max-width: 40em) {
    font-size: ${(props) => props.theme.fontmd};

  }

  @media (max-width: 48em) { 
    align-self: center;
    text-align:center;
  }
  
`

const ButtonContainer = styled.div`
 width: 80%;
  align-self: flex-start;

  @media (max-width: 48em) { 
    align-self: center;
    text-align:center;

    button{
      margin: 0 auto;
    }
  }

`
const TypeWriterText = () => {
  return (
    <>
        <Title>
        Descubra uma nova forma de 
      <Typewriter
      
        options={{
          autoStart: true,
          loop: true,
        }}
        onInit={(typewriter) => {
          typewriter
            .typeString(`<span class="text-1">Ouvir.</span>`)
            .pauseFor(2000)
            .deleteAll()
            .typeString(`<span class="text-2">Colecionar.</span>`)
            .pauseFor(2000)
            .deleteAll()
            .typeString(`<span class="text-3">Investir!</span>`)
            .pauseFor(2000)
            .deleteAll()
            .start();
        }}
      />
      </Title>
    
    <SubTitle><br /><br /><br /></SubTitle>
    <ButtonContainer>
    <Button text="COMPRAR NFT"  />
    
    </ButtonContainer>
    </>
  );
};

export default TypeWriterText;
